.App {
  text-align: center;
}

.App-header {
  background-color: #5677b9;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: hsl(0, 0%, 100%);
}

body{
  width:100%;
  height:100%;
  background-color: #ffffff;
  position: relative;
}

section {
  display: grid;
  place-items: center;
  height: 100vh;
  font-size: 32px;
  
}

h5 {
  font-weight: bold
}

.Introduction {
  font-size: 20px;
}
.nav {
  font-family: sans-serif;
	display: flex;
	align-items: center;
}


.Linkedin-link {
  text-decoration: none; 
  color: rgb(255, 255, 255); 
}


.NavBar {
  display: flex;
  gap: 1rem;
  font-size: 0.9rem;
  list-style: none;
  padding: 0;
}

.NavBar-Links {
  cursor: pointer;
}

.NavBar-Links:hover {
  text-decoration: underline;
}

::-webkit-scrollbar{
  display: none;
}

.tab-list {
  border-bottom: 1px solid #ccc;
  padding-left: 0;
}

.tab-list-item {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 0.5rem 0.75rem;
  font-size: 20px;
}

.tab-list-active {
  background-color: white;
  border: solid #ccc;
  border-width: 1px 1px 0 1px;
  font-size: 20px;
}

.tabs {
  font-size: 20px;
}
